.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

h6{
  color: #c8c8c8;
  letter-spacing: 2px;
  text-align: left;
}
h3{
  background-color: white;
  padding: 30px;
  padding-left: 33rem;
  color: #777B7E;
  font-weight: lighter;
  letter-spacing: 2px;
}
.img_two{
  height:10%;
 
}
section{
  background-color: #c8c8c8 ;
}
p{
  font-weight: bold;
  font-size: 20px;
  text-align: left;
}
.col{
padding-top: 5rem;
padding-bottom: 3rem;
margin-left: 4rem;

}

